import { styled } from "@rp/react/theme"

const IconWrapper = styled("div")(({ theme }) => ({
  paddingInline: theme.spacing(1.25),

  "::after": {
    content: "''",
    position: "absolute",
    height: 10,
    top: -10,
    width: 1,
    left: 24,
    backgroundColor: theme.palette.grey[500],
  },
}))

const DefaultTreeIcon = styled("div", { shouldForwardProp: prop => prop !== "showTicker" })<{
  showTicker?: boolean
}>(({ theme, showTicker }) => ({
  paddingInline: theme.spacing(1.25),
  "::before": {
    content: "''",
    position: "absolute",
    height: 1,
    width: showTicker ? 100 : 24,
    left: 24,
    backgroundColor: theme.palette.grey[500],
  },

  "::after": {
    content: "''",
    position: "absolute",
    height: 40,
    width: 1,
    top: -10,
    left: 24,
    backgroundColor: theme.palette.grey[500],
  },
}))

const LastTreeIcon = styled("div", { shouldForwardProp: prop => prop !== "showTicker" })<{
  showTicker?: boolean
}>(({ theme, showTicker }) => ({
  "::before": {
    content: "''",
    position: "absolute",
    height: 1,
    width: showTicker ? 100 : 24,
    left: 24,
    backgroundColor: theme.palette.grey[500],
  },

  "::after": {
    content: "''",
    position: "absolute",
    height: "50%",
    top: 0,
    width: 1,
    left: 24,
    backgroundColor: theme.palette.grey[500],
  },
}))

type Props = {
  type: "default" | "last"
  showTicker?: boolean
}

function WatchlistNestedSuggestionIcon({ type, showTicker }: Props) {
  return type === "default" ? (
    <DefaultTreeIcon showTicker={showTicker} />
  ) : (
    <IconWrapper data-testid="nested-suggestion-icon">
      <LastTreeIcon showTicker={showTicker} />
    </IconWrapper>
  )
}

export { WatchlistNestedSuggestionIcon }
